html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-name);
}

:root {
  --background: #ebf6f8;
  --orange: #ff5c00;
  --grey-borders: #ebebeb;
  --text: #000000;
  --white: #ffffff;
}

:root {
  --font-open-sans: "Open Sans", sans-serif;
  --font-roboto: "Roboto", sans-serif;
}